.contacts{
    background: url(../img/contacts_bg.png);
    background-size: cover;
    background-color: #011a43;
    height: 670px;
    &__container{
    	width: 90%;
		margin: 0 auto;
		display: flex;
		justify-content: space-between;
		flex-direction: row;
		padding-top: 120px;
		@media screen and (max-width:768px) {
			flex-direction: column;	
			align-items: center;
			padding-top: 55px;			
		}
    	.leftSide{
			width: 50%;
			@media screen and (max-width:768px) {
				display: flex;
				justify-content: center;
				width: 100%;	
				margin-bottom: 40px;
			}
		}
		.rightSide{
			@media screen and (max-width:480px) {
				width: 100%;
			}
			.base-input{
				width: 610px;
				height: 54px;
				border-radius:27px;
				padding-left: 20px;
				border:none;
				color:#969696;
				margin-bottom: 12px;
				line-height: 1.77;
				@media screen and (max-width:480px) {
					width: 100%;
				}
			&::-webkit-input-placeholder {
					font-family: 'Montserrat',sans-serif;
					font-size: 13px;
					font-weight: bold;
					font-style: normal;
					font-stretch: normal;
					line-height: 1.77;
					letter-spacing: normal;
					color:#969696;
				}
			&::-moz-placeholder { /* Firefox 19+ */
 				font-family: 'Montserrat',sans-serif;
				font-size: 13px;
				font-weight: bold;
				font-style: normal;
				font-stretch: normal;
				line-height: 1.77;
				letter-spacing: normal;
				color:#969696;
				}
				&:-moz-placeholder { /* Firefox 19+ */
 				font-family: 'Montserrat',sans-serif;
				font-size: 13px;
				font-weight: bold;
				font-style: normal;
				font-stretch: normal;
				line-height: 1.77;
				letter-spacing: normal;
				color:#969696;
				}
				&:-ms-input-placeholder { /* Firefox 19+ */
 				font-family: 'Montserrat',sans-serif;
				font-size: 13px;
				font-weight: bold;
				font-style: normal;
				font-stretch: normal;
				line-height: 1.77;
				letter-spacing: normal;
				color:#969696;
				}
			}
			.base-textarea{
				width: 610px;
				height: 148px;
				border-radius:14px;
				padding: 16px 0 0 20px;
				margin-bottom: 24px;
				@media screen and (max-width:480px) {
					width: 100%;
				}
				&::-webkit-input-placeholder {
					font-family: 'Montserrat',sans-serif;
					font-size: 13px;
					font-weight: bold;
					font-style: normal;
					font-stretch: normal;
					line-height: 1.77;
					letter-spacing: normal;
					color:#969696;
				}
							&::-moz-placeholder { /* Firefox 19+ */
 				font-family: 'Montserrat',sans-serif;
				font-size: 13px;
				font-weight: bold;
				font-style: normal;
				font-stretch: normal;
				line-height: 1.77;
				letter-spacing: normal;
				color:#969696;
				}
			}
			.footer-form{
				display: flex;
				flex-direction: row;
				justify-content: space-between;
				align-items: center;
				@media screen and (max-width:480px) {
					flex-direction: column;
				}
				.send{
					@media screen and (max-width:375px) {
						width: 100%;
					}
				}
			}
			.label-box{
				display: flex;
				flex-direction: row;
				justify-content: space-between;
				align-items: center;
				@media screen and (max-width:480px) {
					margin-bottom: 30px;
				}
				a{
					color:#f07f2f;
					text-decoration: underline;
				}
			}
			.morka{
				color:#fff;
			}
			#cc_agreement{
				margin-right: 10px;
			}
			.sendbtn{
					border:none;
					border-radius: 100px;
					background: linear-gradient(to right, #ef7d2d, #f9a86f);
					width: 235px;
					height: 50px;
					color:#fff;
					font-family: 'Roboto', sans-serif;
					font-size: 17px;
					font-weight: bold;
					@media screen and (max-width:375px) {
						text-transform: uppercase;
						width: 100%;
					}
				}	
		}
    }
	
}