.products{
	background-image: linear-gradient(177deg, #001942 24%, #164980 62%, #040c33);
	background-repeat: no-repeat;
	background-size: cover;
	height: 1350px;
	position: relative;
	@media screen and (max-width:480px) {
		height: 1750px;
	}
	&__container{
		width: 90%;
		margin: 0 auto;
		display: flex;
		justify-content: space-between;
		flex-direction: column;
		position: absolute;
		top:-150px;
		left: 0;
		right: 0;
		@media screen and (max-width:480px) {
			top: -450px;
		}
	}
	.title h2{
		font-family: 'Roboto', sans-serif;
		font-size: 41px;
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		line-height: normal;
		letter-spacing: -0.5px;
		color: #f1a670;
		margin-bottom: 59px;
		@media screen and (max-width:768px) {
			text-align: center;
		}
		@media screen and (max-width:480px) {
			font-size: 31px;
			letter-spacing: -0.4px;
			text-align: center;
		}
	}
	.productBlock{
		display: flex;
		flex-direction: row;
		margin-bottom: 120px;
		background: linear-gradient(to left, #ef7d2d, #f9a86f);
		border-radius: 9px;
		@media screen and (max-width:480px) {
			flex-direction: column;
			margin-bottom: 55px;
		}
		&.rev{
			flex-direction: row-reverse;
			@media screen and (max-width:480px) {
				flex-direction: column;
			}
		}
		&__video{
			width: 41.80vmax;
			@media screen and (max-width:480px) {
				width: 100%;
			}
			.yt{
				border-radius:20px;
			}
		}
		&__spec{
			display: flex;
			flex-direction: column;
			justify-content: center;
			padding-left: 90px;
			font-family: 'Roboto', sans-serif;
			font-size: 17px;
			font-weight: normal;
			font-style: normal;
			font-stretch: normal;
			line-height: 2.18;
			letter-spacing: normal;
			color: #fff;
			width: 50%;
			&.third{
				.title{
					width: 50%;
				}
				.full{
					width: 100%;
				}
			}
			.block{
				width: 100%;
				.content{
					display: flex;
					flex-direction: row;
					justify-content: flex-start;
					@media screen and (max-width:480px) {
						flex-flow: row wrap;
					}
					.first{
						.title{
							@media screen and (max-width:480px) {
							width: 55%;
					}
						}
						@media screen and (max-width:480px) {
							display: flex;
   							flex-direction: row;
   							justify-content: space-between;
   							width: 94%;
					}
					}
					.second{
						@media screen and (max-width:480px) {
							display: flex;
    						justify-content: space-between;
    						width: 100%;
					}
					}
					.title{
						width: 55%;
						&.empty{
							@media screen and (max-width:480px) {
							width: 53%;
						}
						}
					}
				}
				.text{
					display: flex;
					flex-direction: column;
				}
			}
			
			@media screen and (max-width:768px) {
				padding-left: 30px;
				width: 60%;
			}
			@media screen and (max-width:480px) {
				width: 100%;
				padding-left: 0;
				align-items: center;
				padding: 0 20px 0 20px;
			}
			& h3{
				font-size: 31px;
				line-height: normal;
				@media screen and (max-width:480px) {
					font-size: 24px;
				}
			}		
		}
	}
}